___CSS_LOADER_EXPORT___.push([module.id, `* {
    font-family: "Open Sans", sans-serif;
    font-optical-sizing: auto;
    font-weight: 400;
}

h1, h2 {
    font-family: "Roboto", sans-serif;
    font-weight: 700;
    font-style: normal;
}

body {
    box-sizing: border-box;
    margin: 0;
    --header-height: 7vh;
    --content-height: calc(100vh - var(--header-height));
}

header {
    background-color: rgb(22, 216, 22);
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: var(--header-height);
}

header > nav > button {
    color: #fff;
    background: none;
    border: none;
    font-size: 1.75rem;
    padding: 15px;
    cursor: pointer;
}

header > nav > button:hover {
    color: black;
}

.content {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: linear-gradient(0.25turn, lightpink, #ebf8e1, lightskyblue);
    min-height: var(--content-height);
    /* padding: 30px; */
}

.content > .info {
    background-color: #fff;
    padding: 50px;
    height: var(--content-height);
    font-size: 1.3rem;
    box-shadow: 2px 2px 5px lightgray, -3px 3px 5px lightgray;
}

.info ul {
    list-style: none;
}

h1 {
    text-decoration: underline;
}

.card-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    margin-top: 30px;
    gap: 35px;
}

.card {
    background-color: white;
    border-radius: 10px;
    padding: 30px;
}

.contact-container {
    border-bottom: 3px solid red;
}

.first-contact-container {
    border-top: 3px solid red;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAEA;IACI,oCAAoC;IACpC,yBAAyB;IACzB,gBAAgB;AACpB;;AAEA;IACI,iCAAiC;IACjC,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA;IACI,sBAAsB;IACtB,SAAS;IACT,oBAAoB;IACpB,oDAAoD;AACxD;;AAEA;IACI,kCAAkC;IAClC,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,gCAAgC;AACpC;;AAEA;IACI,WAAW;IACX,gBAAgB;IAChB,YAAY;IACZ,kBAAkB;IAClB,aAAa;IACb,eAAe;AACnB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uEAAuE;IACvE,iCAAiC;IACjC,mBAAmB;AACvB;;AAEA;IACI,sBAAsB;IACtB,aAAa;IACb,6BAA6B;IAC7B,iBAAiB;IACjB,yDAAyD;AAC7D;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,0BAA0B;AAC9B;;AAEA;IACI,aAAa;IACb,2DAA2D;IAC3D,gBAAgB;IAChB,SAAS;AACb;;AAEA;IACI,uBAAuB;IACvB,mBAAmB;IACnB,aAAa;AACjB;;AAEA;IACI,4BAA4B;AAChC;;AAEA;IACI,yBAAyB;AAC7B","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');\n\n* {\n    font-family: \"Open Sans\", sans-serif;\n    font-optical-sizing: auto;\n    font-weight: 400;\n}\n\nh1, h2 {\n    font-family: \"Roboto\", sans-serif;\n    font-weight: 700;\n    font-style: normal;\n}\n\nbody {\n    box-sizing: border-box;\n    margin: 0;\n    --header-height: 7vh;\n    --content-height: calc(100vh - var(--header-height));\n}\n\nheader {\n    background-color: rgb(22, 216, 22);\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    min-height: var(--header-height);\n}\n\nheader > nav > button {\n    color: #fff;\n    background: none;\n    border: none;\n    font-size: 1.75rem;\n    padding: 15px;\n    cursor: pointer;\n}\n\nheader > nav > button:hover {\n    color: black;\n}\n\n.content {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    background: linear-gradient(0.25turn, lightpink, #ebf8e1, lightskyblue);\n    min-height: var(--content-height);\n    /* padding: 30px; */\n}\n\n.content > .info